.Banner {
  width: 100%;
  height: 240px;
  .blur {
    width: inherit;
    height: inherit;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
