.sidebar {
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    padding: 4rem 0;
    height: 100%;
    margin-top: 50px;
  }
  
  
  
  .sidebar > a > img:hover {
    filter: drop-shadow(0 0 10px #EAB543);
  }

 
  .sidebar a {
    text-decoration: none;
    color: rgba(0, 0, 0, 0.493);
    font: 200 1rem "Roboto";
    padding: 2rem;
    transition: all 0.5s;
  }
  .treeView {
    color: rgba(0, 0, 0, 0.493);
    font: 200 1rem "Roboto";
    padding: 2rem;
    transition: all 0.5s;
  }
  .treeView:hover{
    color: #EAB543;
    transform: scale(1.1);
  }
  .sidebar a:hover {
    color: #EAB543;
    transform: scale(1.1);
  }

  .treeView,
  .sidebar a > P {
    display: flex;
    align-items: center;
  }
  .treeView,
  .sidebar a > p > svg {
    margin-right: 0.5rem;
  }
  

  