.productsHeading {
    margin: 2vmax auto;
    width: 30vw;
    color: rgba(0, 0, 0, 0.678);
    font: 500 1.5vmax "Roboto";
    text-align: center;
  }
.productsHeading-wrap {
  display: flex;
  justify-content: center;
  .productsHeadingLink{

    text-decoration: none;

  }

}
 
  #component-helper{
    width: 70%;
    font-size: small;
  }
.products {
    display: flex;
    flex-wrap: wrap;
    padding: 0 ;
    justify-content: center;
    min-height: 55vh;
  }

  .shop-wrap {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
.productsHeading-line{
    margin-top: 0.5rem;
  }
.productsHeading-line::before{
      content: "";
      width: 8rem;
      height: 0.1rem;
      display: block;
      margin: 0 auto;
      background-color: #EAB543;
}
.productsHeading-line::after{
    content: "";
    width: 2rem;
    padding-top: 0.5rem;
    height: 0.2rem;
    display: block;
    background-color: #EAB543;
    margin: 0 auto;
    margin-bottom: 1rem;
}

.paginationBox {
    display: flex;
    justify-content: center;
    margin: 6vmax;
  }

.pagination {
    display: flex;
    justify-content: center;
    padding: 0;
}


.page-item {
    background-color: rgb(255, 255, 255);
    list-style: none;
    border: 1px solid rgba(0, 0, 0, 0.178);
    padding: 1vmax 1.5vmax;
    transition: all 0.3s;
    cursor: pointer;
}
.page-item:first-child {
    border-radius: 5px 0 0 5px;
 }

.page-item:last-child {
    border-radius: 0 5px 5px 0;
 }
.page-link {
    text-decoration: none;
    font: 300 0.9vmax "Roboto";
    color: rgb(80, 80, 80);
    transition: all 0.3s;
}

.page-item:hover {
    background-color: rgb(230, 230, 230);
}

.page-item:hover .page-link {
    color: rgb(0, 0, 0);
}

.pageItemActive {
    background-color: #EAB543;
}

.pageLinkActive {
    color: #1b1a1a;
}

.filterBox,.result-filer {
    width: 100%;
    display: flex;
    gap:10px;
    justify-content: center;
    margin: 20px;

}
.categoryBox {
    padding: 0%;
  }
.category-link {
    list-style: none;
    color: rgba(0, 0, 0, 0.61);
    font: 400 0.8vmax "Roboto";
    margin: 0.5vmax 0.2vmax;
    cursor: pointer;
    transition: all 0.5s;
  }
.category-link:hover {
    color: #EAB543;
  }

.filterBox > fieldset {
    border: 1px solid rgba(0, 0, 0, 0.329);
  }

@media screen and (max-width: 600px) {
    .filterBox {
      width: 40vmax;
      display: block;
      margin: auto;
    }

    .page-link {
      font: 300 1.7vmax "Roboto";
    }
    .category-link {
      font: 400 1.8vmax "Roboto";
    }
  }
